import moment from 'moment';
import { Fragment } from 'react';

const CountdownCard = ({ event }) => {
    const displayFormat = 'YYYY MMM DD hh:mm:ss a';
    const dataFormat = 'YYYY/MM/DD hh:mm:ss';

    const startDate = moment.utc(event.start_time, dataFormat)
    const endDate = moment.utc(event.end_time, dataFormat)
    const totalSecondsLeft = moment.duration(startDate.diff(moment())).asSeconds();
    const secondsLeft = Math.floor(totalSecondsLeft) % 60;
    const minsLeft = Math.floor(totalSecondsLeft / 60 % 60);
    const hoursLeft = Math.floor(totalSecondsLeft / 60 / 60 % 24);
    const daysLeft = Math.floor(totalSecondsLeft / 60 / 60 / 24);

    const totalSecondsToEnd = moment.duration(endDate.diff(moment())).asSeconds();
    const secondsToEnd = Math.floor(totalSecondsToEnd) % 60;
    const minsToEnd = Math.floor(totalSecondsToEnd / 60 % 60);
    const hoursToEnd = Math.floor(totalSecondsToEnd / 60 / 60 % 24);
    const daysToEnd = Math.floor(totalSecondsToEnd / 60 / 60 / 24);

    const inProgress = totalSecondsLeft <= 0;
    const padZero = (number) => `${number}`.padStart(2, '0');
    const showEnd = !(event.start_time === event.end_time);

    return (
        <div className={`countdown-card ${(inProgress) ? 'active' : ''}`}>
            <div className='icon'>
                <img alt={event.icon_src} src={`icons/${event.icon_src}.png`} />
            </div>
            <div className='info'>
                <div className='title'>{event.event_name}</div>
                <div className='countdown'>
                    {(inProgress) ?
                        <div className='subtitle'>--In progress--</div>
                        :
                        <Fragment><div className='subtitle'>Starts in</div><div>{`${daysLeft} days ${padZero(hoursLeft)}:${padZero(minsLeft)}:${padZero(secondsLeft)}`}</div></Fragment>
                    }
                    {showEnd && <Fragment><div className='subtitle'>Ends in</div><div>{`${daysToEnd} days ${padZero(hoursToEnd)}:${padZero(minsToEnd)}:${padZero(secondsToEnd)}`}</div></Fragment>}
                </div>
                <div className='date'>
                    <div >Start Date: {startDate.local().format(displayFormat)}</div>
                    {showEnd && <div >End Date: {endDate.local().format(displayFormat)}</div>}
                </div>
            </div>
        </div >
    )
}

export default CountdownCard;