import React, { useState, useEffect } from 'react';
import _ from 'lodash';
import { v4 as uuidv4 } from 'uuid';
import '../App.css';

const Comp = ({ comp, unitDistance, onClick }) => {
  return <div style={{ minHeight: '68px' }}>
    {_.chain(comp)
      .orderBy(unit => unitDistance[unit], "desc")
      .map(unit => (onClick) ?
        <img onClick={() => onClick(unit)} key={`${unit}-img`} alt={unit} src={`icons/units/${unit}.png`} />
        :
        <img key={`${unit}-img`} alt={unit} src={`icons/units/${unit}.png`} />
      )
      .value()}
  </div>
}

const Matchup = () => {
  const [showLose, setShowLose] = useState(false);
  const [matchup, setMatchup] = useState([]);
  const [unitDistance, setUnitDistance] = useState({});
  const [blacklist, setBlacklist] = useState({});

  const [sortedMatchup, setSortedMatchup] = useState([]);

  const [bestComp, setBestComp] = useState([])

  const [winningMatches, setWinningMatches] = useState([]);
  const [losingMatches, setLosingMatches] = useState([]);
  const [defCharOptions, setDefCharOptions] = useState([]);
  const [attackOptions, setAttackOptions] = useState([]);

  const [selectedDefender, setSelectedDefender] = useState([]);

  useEffect(() => {
    fetch('data/matchup.json')
      .then((r) => r.json())
      .then((json) => setMatchup(json));

    fetch('data/unit_distance.json')
      .then((r) => r.json())
      .then((json) => setUnitDistance(json));

    fetch('data/blacklist.json')
      .then((r) => r.json())
      .then((json) => setBlacklist(json));
  }, []);

  useEffect(() => {
    const sorted = _.chain(matchup)
      .uniq()
      .map(matchup => {
        const key = uuidv4();
        return {
          ...matchup,
          winner: _.orderBy(matchup['winner'], unit => unitDistance[unit], "desc"),
          loser: _.orderBy(matchup['loser'], unit => unitDistance[unit], "desc"),
          key,
        };
      })
      .value();

    setSortedMatchup(sorted)
  }, [matchup, unitDistance]);

  useEffect(() => {
    if (sortedMatchup.length === 0) return;

    const result = _.chain(sortedMatchup)
      .countBy(matchup => matchup.winner)
      .entries()
      .maxBy(_.last)
      .value();
    setBestComp(result[0].split(','));

    setDefCharOptions(_.chain(sortedMatchup).map(matchup => matchup.loser).flatten().uniq().sort().value())
    setAttackOptions(_.chain(sortedMatchup).map(matchup => matchup.winner).flatten().uniq().filter(unit => !_.includes(blacklist, unit)).value())

  }, [sortedMatchup, blacklist]);

  useEffect(() => {
    const result = _.chain(sortedMatchup)
      .filter(matchup => matchup.winType !== 'timeout')
      .filter(matchup => _.intersection(matchup.loser, selectedDefender).length >= 1)
      .filter(matchup => _.intersection(matchup.winner, blacklist).length <= 0)
      .map(winningMatch => {
        const similarity = _.intersection(winningMatch.loser, selectedDefender).length;
        return { ...winningMatch, similarity };
      })
      .uniq()
      .orderBy(matchup => matchup.similarity, "desc")
      .chunk(4)
      .value();
    if (result.length > 0) { setWinningMatches(result[0]) } else { setWinningMatches([]) };
  }, [selectedDefender, sortedMatchup, blacklist]);

  useEffect(() => {
    const result = _.chain(sortedMatchup)
      .filter(matchup => _.intersection(matchup.winner, selectedDefender).length >= 2)
      .map(losingMatch => {
        const similarity = _.intersection(losingMatch.winner, selectedDefender).length;
        return { ...losingMatch, similarity };
      })
      .uniq()
      .orderBy(matchup => matchup.similarity, "desc")
      .chunk(4)
      .value();
    if (result.length > 0) { setLosingMatches(result[0]) } else { setLosingMatches([]) };
  }, [selectedDefender, sortedMatchup]);

  const onDefClick = (name) => {
    if (selectedDefender.length < 5) {
      setSelectedDefender(_.chain([...selectedDefender, name]).uniq().value())
    }
  }

  const onClearClick = () => {
    setSelectedDefender([])
  }

  const removeUnitHandler = (unit) => {
    setSelectedDefender(selectedDefender.filter(defender => defender !== unit))
  }

  const onClickShowLose = () => {
    setShowLose(!showLose);
  }

  return (
    <div className="content">

      <div className="suggestion-container">
        {(!showLose) ?
          <div className="wins">
            <h4 onClick={onClickShowLose}>Winning matchups</h4>
            {winningMatches.map(comps => (
              <div key={comps.key} className="comp-container">
                <div>
                  <div>Winner</div>
                  <Comp comp={comps.winner} unitDistance={unitDistance} />
                </div>
                <div>
                  <div>Loser(Same Units: {comps.similarity})</div>
                  <Comp comp={comps.loser} unitDistance={unitDistance} />
                </div>
              </div>
            ))}
          </div>
          :
          <div className="loses">
            <h4 onClick={onClickShowLose}>Losing matchups</h4>
            {losingMatches.map(comps => (
              <div key={comps.key} className="comp-container">
                <div>
                  <div>Winner(Same Units: {comps.similarity})</div>
                  <Comp comp={comps.winner} unitDistance={unitDistance} />
                </div>
                <div>
                  <div>Loser</div>
                  <Comp comp={comps.loser} unitDistance={unitDistance} />
                </div>
              </div>
            ))}
          </div>
        }

        {winningMatches.length === 0 &&
          <div className="comp-container">
            <div>Best Performing Comp</div>
            <Comp comp={bestComp} unitDistance={unitDistance} />
          </div>}

      </div>

      <div className='selection-container'>

        <div>
          <div>Selected Defenders</div>
          <Comp onClick={removeUnitHandler} comp={selectedDefender} unitDistance={unitDistance} />
          <div><button style={{ height: "50px" }} onClick={onClearClick}>Clear team</button></div>
        </div>

        <div>
          <div>Select</div>
          <div className="defButtons">
            {defCharOptions.map(name => (
              <div key={`${name}-icon`} className="button-container" onClick={onDefClick.bind(this, name)} >
                <img alt={name} src={`icons/units/${name}.png`} />{name}
              </div>))}
          </div>
        </div>

        <div>
          <div>Available Units</div>
          <Comp comp={attackOptions} unitDistance={unitDistance} />
        </div>

      </div>

    </div>
  );
}

export default Matchup;
